import React from "react";
import axios from 'axios';
import './index.css'
const commands={
  clear:'clear',
  en_live:'en_live',
  dis_live:'dis_live'
};
const UseOnEnter=()=>{
  const [consoleOutput,updateConsoleOutput]=React.useState([]);
  async function onEnter(value,key){
    if(key==="Enter"){
      const command=commands[value]||value;
      let result=<span/>;
      if(commands[value]==="clear"){
        return updateConsoleOutput(consoleOutput.filter(()=>false));
      }
      let response=await axios.post('https://www.milad-karami.ir/do_command',{command:command});
      response=response.data;
      if(response['type']==='img'){
        let imgUrl=`https://www.milad-karami.ir/${response['response_message']}`
        result=<img className='get-image' src={imgUrl} alt={imgUrl}/>;
      }else{
        result=<span>{response['response_message']}</span>;
      }
      return updateConsoleOutput(consoleOutput=>
        consoleOutput.concat({command,result})
      );
    }
  }
  return [consoleOutput,onEnter];
};
export default UseOnEnter;
